.about_container {
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  column-gap: 4rem;
}

.section_title {
  margin-bottom: 2rem;
}

.about_img {
  width: 350px;
  border-radius: 1.5rem;
  justify-self: center;
}

.about_info {
  grid-template-columns: repeat(3, 140px);
  gap: 0.5rem;
  margin-bottom: var(--mb-2);
}

.about_box {
  background-color: var(--container-color);
  border: 1px solid rgb(0, 0, 0, 0.1);
  border-radius: 0.75rem;
  text-align: center;
  padding: 1rem 1.25rem;
}

.about_icon {
  font-size: 1.5rem;
  color: var(--title-color);
  margin-bottom: var(--mb-0-5);
}

.about_title {
  font-size: var(--small-font-size);
  font-weight: var(--font-medium);
}

.about_subtitle {
  font-size: var(--tiny-font-size);
}

.about_discription {
  padding: 0 4rem 0 0;
  margin-bottom: var(--mb-2-5);
}

/* For large devices */
@media screen and (max-width: 992px) {
  .about_container {
    grid-template-columns: 1fr;
    row-gap: 2.5rem;
  }

  .about_img {
    width: 220px;
  }

  .about_box {
    padding: 0.75rem 0.5rem;
    width: 8.75rem;
    height: 7rem;
  }

  .about_data {
    text-align: center;
  }

  .about_info {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .about_discription {
    padding: 0 5rem;
    margin-bottom: 2rem;
  }
}

/* For medium devices */
@media screen and (max-width: 576px) {
  .about_discription {
    padding: 0;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .about_info {
    margin: 0 0.3rem 1rem 0.3rem;
  }

  .about_discription {
    margin: 0 0.3rem 1rem 0.3rem;
    padding: 0;
  }
}
